
.button {
    background-color: #8bb4d9;
    color: #172d48;
    font-size: 16px;
    border: 1px solid #172d48;
    border-radius: 4px;
    height: 1.5rem;
}
.button:hover {
    background-color: #83a9cb;
    box-shadow: inset 0 0 2px #000000;
}
.button:active {
    font-size: 15px;
}